*{
    padding: 0;
    margin: 0;
    line-height: 1.5em;
    box-sizing: border-box;
}

a{
    color: #FEFEFE;
    text-decoration: none;
}
.fa-home, .fa-user, .fa-info-circle, .fa-cog{
    display: none;
}
body{
    width: 100%;
    background: #090E12;
    color: #FEFEFE;
    font-family: 'Times New Roman', Times, serif;
}

/* styling for Navigation component starts here */
nav{
    width: 100%;
    height: 120px;
    background: #090E12;
    color: #FEFEFE;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
}
nav div{
    width: 45%;
}
nav li{
    list-style: none;
    margin: 0 30px 0 0;
}
.nav1 ul{
    display: flex;
    margin: 30px 0 0 20%;
}
.nav2 ul{   
    display: flex;
    margin: 30px 0 0 40%;
}
.span1{
    width: 40%;
    height: 40px;
    padding: 5px 0 0 35px;
    margin: 0 40px 0 0;
    background: #090E12;
    color: #FD961A;
    border: 1px solid #FD961A;
    border-radius: 7px;
}

.span2{
    width: 41%;
    height: 40px;
    padding: 5px 0 0 35px;
    margin: 0 40px 0 0;
    background: #FD961A;
    color: #090E12;
    border: 1px solid #FD961A;
    border-radius: 7px;
}


/* styling for Hearder component (the backcground image with opacity) starts here*/
.header{
    width: 100%;
    height: 370px;
    background: url('./images/pexels-stefan-stefancik-91227.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    opacity: 4%;
    margin: 9% 0 0 0;
}

/* styling for the wrapper (the white background) starts here*/
.wrapper{
    width: 100%;
    height: 150px;
    background: #FEFEFE;
}

/*  the images showing on the white background. they are all inside the img_Cards component*/
.img1{
    width: 20%;
    height: 300px;
    position: absolute;
    top: 53%;
    left: 2%;
    background: url('./images/pexels-andrea-piacquadio-712513.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 9px;
}
.img2{
    width: 20%;
    height: 300px;
    position: absolute;
    top: 53%;
    left: 28%;
    background: url('./images/afunwa1.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 9px;
}
.img3{
    width: 20%;
    height: 300px;
    position: absolute;
    top: 53%;
    left: 53%;
    background: url('./images/pexels-rodnae-productions-5530626.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 9px;
}
.img4{
    width: 20%;
    height: 300px;
    position: absolute;
    top: 53%;
    left: 78%;
    background: url('./images/amaka.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 9px;
}

/* Styling for About_Us page starts here*/
.about_us{
    width: 100%;
    color: #FEFEFE;
    font-size: large;
    padding: 50px 30px 0 30px;
    margin: 9% 0 0 0;
}

/* Styling for profile page starts here*/
.profile{
    width: 100%;
    color: #FEFEFE;
    margin: 14% 0 0 0;
}
.profile_div{
    width: 55%;
    display: flex;
    justify-content: center;   
}
.userName{
    width: 25%;
    height: 30px;
    margin: 0 20px 0 -50%;
}
.profile_pic{
    width: 12%;   
    height: 150px;
    position: absolute;
    top: 22%;
    left: 22%;
    background: url('../src/images/afunwa1.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border-radius: 50%;   
}

/* the item container containes the links to images, videos, audios, and documents that are inside profile page*/
.item_container{
    width: 100%;
    margin: 12% 0 0 0;
}
.item_container a{
    padding: 0 0 0 12%;
    font-size: large;
}
.item_container span{
    color: #FD961A;
}


/* Styling for upload page starts here*/
.upload{
    width: 100%;
    color: #FEFEFE;
    margin: 11% 0 0 0;
}


/* select is used to provide option for where to store a given file (it is in the upload page)*/
select{
    width: 30%;
    height: 50px;
    margin: 0 0 20px 30%;
    font-size: large;
    text-align: center;
}

/* textarea is used to provide caption for the uploaded file (it is in the upload page)*/
textarea{
    width: 30%;
    height: 150px;
    margin: 20px 0 0 30%;
    outline: none;
    padding: 5px 0 0 20px;
}

/* Styling for settings page starts here*/
.settings{
    width: 100%;
    color: #FEFEFE;
    margin: 12% 0 0 0;
}

/* Styling for signIn page starts here*/
.signIn{
    width: 100%;
    color: #FEFEFE;
    margin: 9% 0 0 0;
}
form{
    width: 100%;
    
}
.label1, .label3{
    width: 40%;
    display: block;
    margin: 15% 0 10px 30%;
    font-size: large;
    font-weight: bold;
}

.label2, .label4, .label5, .label6, .label7, .label8, .label9, .label10, .label11, .label12{
    width: 40%;
    display: block;
    margin: 20px 0 10px 30%;   
    font-size: large;
    font-weight: bold;
}

input{
    display: block;
    width: 40%;
    height: 50px;
    margin: auto;
    outline: none;
    padding: 0 0 0 10px;
    font-size: large;
    font-weight: bold;
}
input[type=text], input[type=password], input[type=email]{
    border: none;
    background: transparent;
    border-bottom: 1px solid white;
    color: #FEFEFE;
}



/* Styling for all the buttons used in the website*/
.button, .button1, .button2{
    width: 10%;
    height: 45px;
    margin: 30px 0 30px 42%;
}
.span a{
    color: #FD961A;
    margin: 0 0 0 42%;
    font-size: large;
}


/* Styling for signUp page starts here*/
.signUp{
    width: 100%;
    color: #FEFEFE;
    margin: 9% 0 0 0;
}

/* Styling for the terms and conditions page starts here*/
.terms{
    width: 100%;
    color: #FEFEFE;
    font-size: large;
    padding: 50px 30px 0 30px;
    margin: 9% 0 0 0;
}

/* Styling for forgotten password page starts here (contained in the signIn page)*/
.forgot_password{
    width: 100%;
    color: #FEFEFE;
    margin: 15% 0 0 0;
}
.forgot_password input{
    margin: 0 0 20px 30%;
}
.forgot_password p{
    color: #FD961A;
    padding: 0 0 0 30%;
}
/* The footer is in the footer component*/
.footer{
    width: 100%;
    height: 90px;
    padding: 30px 20px 0 20px;
    background: #090E12;
    color: #FEFEFE;
    display: flex;
    justify-content: space-between;
}

@media screen and (max-width:575px) {

    .fa-home, .fa-user, .fa-info-circle, .fa-cog{
        font-size: 23px;
        display: block;
        padding: 35px 30px 0 0;      
    }
    .fa-cog{
        padding: 35px 10px 0 0;
    }
    nav{
        width: 100%;
        position:static;
    }
    nav div{
        width: 80%;
    }
    .nav1{
        display: none;
    }
    .nav2{
        width: 53%;
    }
    .nav2 ul{
        margin: 30px 0 0 2%;
    }
    .span1{
        width: 25%;
        height: 40px;
        border: none;
        padding: 4px 0 0 15px;
        margin: 0 6px 0 0;       
        font-size: x-small;
    }
    .span2{
        width: 25%;
        height: 40px;
        background: #090E12;
        color: #FD961A;
        border: none;
        padding: 4px 0 0 17px;
        font-size: x-small;
    }
   .header{
    margin: 0;
   }
   .img1{
    width: 80%;
    height: 310px;
    top: 63%;
    left: 10%;
   }
   .img2{
    width: 80%;
    height: 310px;
    top: 127%;
    left: 10%;
   }
   .img3{
    width: 80%;
    height: 310px;
    top: 190%;
    left: 10%;
   }
   .img4{
    width: 80%;
    height: 310px;
    top: 252%;
    left: 10.4%;
   }
   .wrapper{
    background: #090E12;
    height: 1460px;
   }
   .signIn{
    margin: 35% 0 0 0;
   }
   .signIn .button{  
    width: 25%;
    margin: 30px 0 30px 38%;
    border-radius: 4px;
    font-size: small;
   }
   .span a{
    font-size: small;
    margin: 30px 0 30px 30%;
   }
   .label1, .label3{
    font-size: small;
    margin: 25% 0 10px 20%;
    display: none;
   }
   .label11, .label12{
    font-size: small;
    margin: 20px 0 10px 20%;
   }
   .label1, .label2, .label4, .label5, .label6, .label7, .label8, .label9, .label10{
    font-size: small;
    margin: 20px 0 10px 20%;
    display: none;
   }
   ::placeholder{
    text-align: center;
   }
   input{
    width: 80%;
    margin: 0 0 20px 20px;
   }
   .button, .button1, .button2{
    font-size: small;
    width: 25%;
    border-radius: 4px;
   }
   .forgot_password{
    margin: 20% 0 0 0;
   }
   .forgot_password input{
    margin: 30px 0 20px 20px;
   }
   .forgot_password p{
    padding: 0 0 0 20px;
    font-size: small;
   }
   .profile{
    margin: 30% 0 0 0;
   }
   .profile_div{
    width: 100%;
   }
   .userName{
    width: 55%;
    margin: 0 20px 0 -30%;
    font-size: small;
   }
   .profile_pic{
    width: 40%;
    top: 30%;
    left: 45%;
   }
   .item_container{
    margin: 45% 0 0 0;
   }
   .item_container a{
    font-size: small;
    padding: 0 0 0 3%;
   }
   .upload{
    margin: 25% 0 0 0;
   }
   select{
    width: 40%;
    margin: 0 0 20px 30%;
    font-size: small;
   }
   textarea{
    width: 40%;
    height: 120px;
    margin: 0 0 20px 30%;
   }
   .label10, .label11{
    margin: 0 0 20px 30%;
   }
   .about_us{
    font-size: small;
    margin: 2% 0 20px 0;
    padding: 50px 10px 0 10px;
   }
   .terms{
    margin: 2% 0 0 0;
    font-size: small;
   }
   .footer h3{
    font-size: x-small;
   }
}